/* CSS for the progress bars and qualifications */
.container-progress {
  text-align: center;
  margin-top: 7%;
}

.container-skill {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  
}

.skill {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.skill-name {
  width: 15%;
  text-align: left; /* Align skill names on the left */
  padding-left: 10px; /* Add padding for spacing */
  font-family: Nunito;
  
}

.skill-progress-container {
  width: 70%; /* Centered with 70% width */
  background-color: rgba(211, 211, 211, 0.408);
  height: 20px;
  display: flex; /* Center progress bar horizontally */
  /*justify-content: center; /* Center progress bar horizontally */
  align-items: center; /* Center progress bar vertically */
   box-shadow: 2px 2px 5px #ffdab98c; /* Add a shadow */
}

.skill-progress {
  width: 0;
  height: 100%;
  background-color: lightgrey;
  transition: width 0.5s ease-in-out;
}

.skill-qualification {
  width: 15%;
  text-align: right; /* Align skill qualifications on the right */
  padding-right: 10px; /* Add padding for spacing */
  font-family: Nunito;
}

/* reight column información */
.Education p{
  font-family: Nutino;
  margin-top: 5%;
  margin-bottom: 5%;
  font-size: 1.2em;
  text-align: justify;

}
