/* Default styles for larger screens */
:root {
  --main-color: #3498db;
  --secondary-color: #e74c3c;
  --background-color: #f2f2f2;
  --pumpink-orange: #FF7518;
  --authum-leaves: #FFA500;
  --Harvest-Gold: #DA9100;
  --Maple-Syrup: #C55A11;
  --Rustic-Orange: #D95C29;
  --Copper-Penny: #AD6242;
  --Bronze: #CD7F32;
  --Caramel: #FFD700;
  --Burnt-Sienna: #E97451;
  --Cinnamon: #D2691E;
  --Light-Burnt-Sienna: #FFA07A;
  --Light-Cinnamon:#FFB366;
  --Light-Rustic-Orange: #FFAB8F;
  --black-therme: #000;
  --white-them: #fff;
  --gray-theme: #ccc;
  --secondary-color:#ffdab9;
  --Header-font1: 'Alegreya sans';
  --Header-font2: 'Nunito';
  --font-size1: 2em;
  --font-size2:1.5em;
  --font-size3:1em;
  --font-size4:1.2em;
  --font-size: 16px;
}

button{
  color: var(--Light-Rustic-Orange);
}
.container-about {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Two columns for larger screens */
  gap: 20px; /* Add gap between columns */
  align-items: center;
  padding: 5%;
  background-color: var(--black-therme);
}

.container-style-image {
  position: relative;
  border: 1px solid var(--Light-Rustic-Orange); /* Add a border to the container */
  padding: 20px; /* Add padding to the container */
  background-color: var(--black-therme); /* Set the background color of the container */
}

.corner1 {
  width: 80px;
  height: 80px;
  background-color: var(--background-color); /* Gray background for the L shape */
  position: absolute;
  z-index: 1; /* Place the corners above the image container */
}

.top-left {
  top: 0;
  left: 0;
}

.bottom-right {
  bottom: 0;
  right: 0;
}

.custom-container-image {
  position: relative;
  z-index: 2; /* Place the image container above the corners */
  background-color: #000; /* Black background for the image container */
  padding: 15px; /* Add padding around the image */
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.custom-image {
  max-width: 80%;
  max-height: 80%;
  object-fit: cover;
  display: block;
}

.custom-container-right-about{
    color: var(--Light-Rustic-Orange);
    font-size: var(--font-size3);
    
}
.custom-container-right-about p{
    color: var(--Light-Rustic-Orange);
    font-family: var(--Header-font2);
}

/* Media query for smaller screens (e.g., mobile devices) */
@media (max-width: 768px) {
  .container-about {
    grid-template-columns: 1fr; /* One column for smaller screens */
  }
}
/*Estyle line*/
/* YourComponent.css */
.container {
  text-align: center;
  position: relative;
  padding: 20px;
  margin-top: 10%;
}

.horizontal-line {
  border: none;
  border-top: 2px solid var(--background-color); /* Adjust the line color and thickness as needed */
}

.text-container {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

.centered-text {
  background-color: var(--black-therme); /* Background color behind the text */
  padding: 0 20px; /* Add padding to the text */
  display: inline-block;
  position: relative;
  z-index: 1;
}
