@import '~font-awesome/css/font-awesome.css'; 
:root {
  --main-color: #3498db;
  --secondary-color: #e74c3c;
  --background-color: #f2f2f2;
  --pumpink-orange: #FF7518;
  --authum-leaves: #FFA500;
  --Harvest-Gold: #DA9100;
  --Maple-Syrup: #C55A11;
  --Rustic-Orange: #D95C29;
  --Copper-Penny: #AD6242;
  --Bronze: #CD7F32;
  --Caramel: #FFD700;
  --Burnt-Sienna: #E97451;
  --Cinnamon: #D2691E;
  --Light-Burnt-Sienna: #FFA07A;
  --Light-Cinnamon:#FFB366;
  --Light-Rustic-Orange: #FFAB8F;
  --black-therme: #000;
  --white-them: #fff;
  --gray-theme: #ccc;
  --secondary-color:#ffdab9;
  --Header-font1: 'Alegreya sans';
  --Header-font2: 'Nunito';
  --font-size1: 2em;
  --font-size2:1.5em;
  --font-size3:1em;
  --font-size4:1.2em;
  --font-size: 16px;
}

.Header{
    background-color: #000000;
    display: flex;
}

/* ... Your existing CSS ... */

.container-left {
  flex: 1; /* Distribute remaining space equally between left and right containers */
  display: flex;
  align-items: center;
  max-width: 50%; /* Set max width to 50% for equal width of left and right containers */
  padding: 20px;
}

.imgheader {
  max-width: 80%; /* Adjust the width of the image as needed */
  height: auto;
  display: block;
  height: 100%;
  margin-left: 20%;
}

.rectangles {
  display: flex;
  justify-content: center; /* Center rectangles horizontally */
  height: 100%;
  margin-bottom: 5%;
}

.rectangle {
  width: 60px;
  height: 100%;
  margin-top: 8%; /* Add margin to create space between the rectangles */
  margin-left: 5%;
}

.gray {
  background-color: #CCCCCC;
}

/*Configuration header right*/
.container-right {
  flex: 1; /* Distribute remaining space equally between left and right containers */
  display: flex;
  flex-direction: column; /* Display children vertically */
  align-items: center; /* Center horizontally */
  max-width: 50%; /* Set max width to 50% for equal width of left and right containers */
}
.buttonsheader ul{
    list-style: none;
    display: flex;
}
.buttonsheader ul>li{
    margin-left: 40px;
}

.custom-icons {
 display: flex; /* Use flexbox */
 align-items: center; 
 margin-top: 5%;

}

.custom-icon {
  font-size: 24px; /* Set the desired font size for icons */
  margin-right: 20px; /* Adjust spacing between icons if needed */
}

.custom-img {
  width: 24px; /* Set the desired width for the image */
  height: 24px; /* Set the desired height for the image */
  filter: grayscale(100%);
}
.custom-icons-container, .buttonsheader-container {
  display: flex;
  justify-content: flex-end; /* Align custom-icons to the right */
  width: 100%; /* Ensure custom-icons take the full width */
  margin-right: 25%;
}

.description-sections h1{
    color: var(--Light-Rustic-Orange);
    /* Titles */
    font-family: Alegreya Sans;
    font-size: 2rem;
    text-align: left;
    margin-bottom: 60px;
    
   
}
.description-sections h2{
    color: var(--gray-theme);
    /* Titles */
    font-family: Nunito;
    font-size: 1.5rem;
    text-align: left;
    margin-bottom: 60px;
}
.description-sections p{
    color: var(--Light-Rustic-Orange);
    /* Body style 1 */
    font-family: Nunito;
    font-size: 1.5rem;
    text-align: left;
    margin-bottom: 40px;
}
button{
    width: 7rem;
    height: 2rem;
    padding: 1.3srem 0.75rem;
    justify-content: center;
    align-items: center;
    border-radius: 2rem;
    font-family: Nunito;
    
}
.buttonsheader-container{
    margin-bottom: 0;
}
a{
    color: white;
}
.linksecondheader{
    display: none;
}
@media screen and (max-width:767px) {
    .container-left {
    /*max-width: 50%; /* Adjust the width to your preference */
    /*padding: 10px; /* Adjust padding for smaller screens */
    flex-direction: column; /* Change flex direction to stack elements vertically */
    align-items: center; /* Center items vertically */
  }
  
  .container-right {
    max-width: 50%; /* Adjust the width to your preference */
    padding: 10px; /* Adjust padding for smaller screens */
  }
  .rectangles{
    display: none;
  }
  .description-sections h1 {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
   .description-sections h2 {
    font-size: 1rem;
    margin-bottom: 30px;
  }
   .description-sections p {
    font-size: 1rem;
    margin-bottom: 10px;
  }
  .buttonsheader {
    display: none;
  }
  .linksecondheader{
    display: inline;
  }
  .linksecondheader ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #CCCCCC;
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
    }
    .linksecondheader ul> li {
    float: left;
}
.linksecondheader  ul> li a {
    display: block;
    color: #ffffff;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-family: Nunito;
    }

.linksecondheader ul>li a:hover {
    background-color: #000000;
}
.imgheader{
    max-width: 100%;
    margin-left: 0%;
  }

}
/* Media query for screens with a width of 414px or less */
@media (max-width: 414px) {
  .Header {
    flex-direction: column; /* Display header elements in one column */
  }
  /* Add any additional styling as needed for this screen size */
  .container-left {
    max-width: 100%;
    padding: 0;
  }

  .container-right {
    max-width: 100%;
  }
  .imgheader{
    max-width: 100%;
    margin-left: 0%;
  }
}

