@import '~font-awesome/css/font-awesome.css'; 
:root {
  --main-color: #3498db;
  --secondary-color: #e74c3c;
  --background-color: #f2f2f2;
  --pumpink-orange: #FF7518;
  --authum-leaves: #FFA500;
  --Harvest-Gold: #DA9100;
  --Maple-Syrup: #C55A11;
  --Rustic-Orange: #D95C29;
  --Copper-Penny: #AD6242;
  --Bronze: #CD7F32;
  --Caramel: #FFD700;
  --Burnt-Sienna: #E97451;
  --Cinnamon: #D2691E;
  --Light-Burnt-Sienna: #FFA07A;
  --Light-Cinnamon:#FFB366;
  --Light-Rustic-Orange: #FFAB8F;
  --black-therme: #000;
  --white-them: #fff;
  --gray-theme: #ccc;
  --secondary-color:#ffdab9;
  --Header-font1: 'Alegreya sans';
  --Header-font2: 'Nunito';
  --font-size1: 2em;
  --font-size2:1.5em;
  --font-size3:1em;
  --font-size4:1.2em;
  --font-size: 16px;
}

.contact-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  font-family: var(--Header-font2);
  color: var(--Light-Rustic-Orange);
}


.column1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.column1 >p{
  font-family: var(--Header-font1);
  color: var(--Light-Rustic-Orange);
  font-size: var(--font-size3);
}

.column2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-group {
  margin-bottom: 20px;
  width: 100%;
}

label {
  display: block;
  margin-bottom: 5px;
  text-align: left;
}

input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.error {
  color: red;
  font-size: 12px;
  margin-top: 5px;
  text-align: center;
}

button {
  background-color: var(--Light-Burnt-Sienna);
  color: var(--white-them);
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: var(--gray-theme);
}

input[type="email"]:focus,
textarea:focus {
  border-color: var(--Light-Rustic-Orange); /* Change the border color to your desired color */
  outline: none; /* Remove the default focus outline (if desired) */
}
/* Adjust styles for screens smaller than 768px */
@media (max-width: 768px) {
  .contact-form {
    grid-template-columns: 1fr; /*one-column layout */
  }

  .column1,
  .column2 {
    text-align: center; /* Center content for smaller screens */
  }
  input[type="email"],
  textarea {
  width: 100%; /* Use percentage for width */
  padding: 2%; /* Use percentage or em units for padding */
}

}
