/* Language Icon Styles */
:root {
  --main-color: #3498db;
  --secondary-color: #e74c3c;
  --background-color: #f2f2f2;
  --pumpink-orange: #FF7518;
  --authum-leaves: #FFA500;
  --Harvest-Gold: #DA9100;
  --Maple-Syrup: #C55A11;
  --Rustic-Orange: #D95C29;
  --Copper-Penny: #AD6242;
  --Bronze: #CD7F32;
  --Caramel: #FFD700;
  --Burnt-Sienna: #E97451;
  --Cinnamon: #D2691E;
  --Light-Burnt-Sienna: #FFA07A;
  --Light-Cinnamon:#FFB366;
  --Light-Rustic-Orange: #FFAB8F;
  --black-therme: #000;
  --white-them: #fff;
  --gray-theme: #ccc;
  --secondary-color:#ffdab9;
  --Header-font1: 'Alegreya sans';
  --Header-font2: 'Nunito';
  --font-size1: 2em;
  --font-size2:1.5em;
  --font-size3:1em;
  --font-size4:1.2em;
  --font-size: 16px;
}
.card-lenguages {
    width: 100%; /* Set the width to 100% to fill the circle */
    height: 100%; /* Set the height to 100% to fill the circle */
    object-fit: contain; /* Maintain the aspect ratio */
    border-radius: 50%; /* Make the image circular */
    filter: invert(83%) sepia(6%) saturate(65%) hue-rotate(318deg) brightness(100%) contrast(89%);
}

.lenguage {
    display: flex;
    justify-content: space-around; /* Center the cards horizontally */
    flex-wrap: wrap; /* Allow cards to wrap to the next row on smaller screens */
}

.circle-container {
    width: 150px; /* Adjust the width of the circular container */
    height: 150px; /* Adjust the height of the circular container */
    border-radius: 50%; /* Make the container circular */
    border: 10px solid #fff; /* Add a black border */
    background-color: #fff; /* Set the background color (gray border) */
    margin: 10px; /* Add margin between circles */
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 5px 5px var(--Light-Rustic-Orange)
}

.circle-inner {
    width: 100%; /* Set the width of the inner circle to 100% */
    height: 100%; /* Set the height of the inner circle to 100% */
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%; /* Make the inner circle circular */
    border: 3px solid gray; /* Add a gray border to the inner circle */
    overflow: hidden; /* Hide the overflow (for circular shape) */
    position: relative;
    cursor: pointer;
}

.container-lenguages h1 {
    font-family: Alegreya sans;
    font-size: var(--font-size);
    color: var(--gray-theme)
}

.hover-message {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--Light-Rustic-Orange); /* Adjust the background color and opacity as needed */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    color: white; /* Adjust the text color as needed */
    opacity: 0; /* Initially hidden */
    transition: opacity 0.3s ease-in-out;
}

.circle-inner:hover .hover-message {
    opacity: 1; /* Show the message on hover */
}

.container-lenguages h1 {
    color: gray;
    font-size: 3.5rem;
    font-family: Alegreya sans;
}

/* ... (other CSS rules for your card) */
