@import '~font-awesome/css/font-awesome.css'; 
:root {
  --main-color: #3498db;
  --secondary-color: #e74c3c;
  --background-color: #f2f2f2;
  --pumpink-orange: #FF7518;
  --authum-leaves: #FFA500;
  --Harvest-Gold: #DA9100;
  --Maple-Syrup: #C55A11;
  --Rustic-Orange: #D95C29;
  --Copper-Penny: #AD6242;
  --Bronze: #CD7F32;
  --Caramel: #FFD700;
  --Burnt-Sienna: #E97451;
  --Cinnamon: #D2691E;
  --Light-Burnt-Sienna: #FFA07A;
  --Light-Cinnamon:#FFB366;
  --Light-Rustic-Orange: #FFAB8F;
  --black-therme: #000;
  --white-them: #fff;
  --gray-theme: #ccc;
  --gray-background: gray;
  --secondary-color:#ffdab9;
  --Header-font1: 'Alegreya sans';
  --Header-font2: 'Nunito';
  --font-size1: 2em;
  --font-size2:1.5em;
  --font-size3:1em;
  --font-size4:1.2em;
  --font-size: 16px;
}
/* YourCardComponent.css */
.card-button {
  display: flex;
  justify-content: space-between; /* Add space between text and icon */
  align-items: center; /* Vertically align text and icon */
  padding: 10px; /* Adjust padding as needed */
}

.card-button .fa {
  margin-left: 10px; /* Adjust the margin between text and icon */
}
/* YourCardComponent.css */

/* Style for the container that holds all the cards */
.card-container {
  display: flex;
  justify-content: space-around; /* Center the cards horizontally */
  flex-wrap: wrap; /* Allow cards to wrap to the next row on smaller screens */
  
}

/* Style for each individual card */
.card {
  width: 200px; /* Adjust the width of the card as needed */
  border: 1px solid var(--Light-Rustic-Orange); /* Add a border to the card */
  padding: 20px; /* Add padding around the card content */
  text-align: center; /* Center the card content */
  margin: 10px; /* Add margin between cards */
  
}
.card p{
  color: var(--gray-background);
  font-family: var(--Header-font2);
}
/* Style for the card image */


/* Style for the card title */
.card-title {
  font-size: 18px; /* Adjust the title font size */
  margin-top: 10px; /* Add space between the image and title */
}

/* Style for the card description */
.card-description {
  margin-top: 10px; /* Add space between the title and description */
}

/* Style for the card button */
/* YourCardComponent.css */

/* ... (previous CSS rules) */

/* Style for the card button */
.card-button {
  width: 100%; /* Make the button span the full width of the container */
  padding: 10px; /* Adjust padding as needed */
  background-color: var(Light-Rustic-Orange); /* Background color of the button */
  color: #fff; /* Text color of the button */
  border: none; /* Remove button border */
  cursor: pointer; /* Add a pointer cursor on hover */
  box-shadow: 0 5px 5px #CCCCCC;
  font-size: 1rem;
}

/* ... (rest of the CSS rules) */


.card-button .fa {
  margin-left: 10px; /* Adjust the margin between text and icon */
  font-size: 16px; /* Adjust the icon size */
}

/* Hover effect for the button */
.card-button:hover {
  background-color: #CCCCCC; /* Change the background color on hover */
}
/* YourCardComponent.css */

/* Style for the card image */
.card-image {
  width: 100%; /* Adjust the width as needed */
  height: auto; /* Maintain the aspect ratio */
  filter: grayscale(100%)  hue-rotate(100deg) brightness(1.0) contrast(1.2);
  color: #ffdab9;
  box-shadow: 0 5px 5px #ffdab9 ;
}

/* ... (other CSS rules for your card) */
.container-wicd h1{
  color: var(--gray-background);
  font-family: Alegeya sans;
  font-size: 2.9rem;
}

