  --authum-leaves: #FFA500;
  --Harvest-Gold: #DA9100;
  --Maple-Syrup: #C55A11;
  --Rustic-Orange: #D95C29;
  --Copper-Penny: #AD6242;
  --Bronze: #CD7F32;
  --Caramel: #FFD700;
  --Burnt-Sienna: #E97451;
  --Cinnamon: #D2691E;
  --Light-Burnt-Sienna: #FFA07A;
  --Light-Cinnamon:#FFB366;
  --Light-Rustic-Orange: #FFAB8F;
  --black-therme: #000;
  --white-them: #fff;
  --gray-theme: #ccc;
  --secondary-color:#ffdab9;
  --Header-font1: 'Alegreya sans';
  --Header-font2: 'Nunito';
  --font-size1: 2em;
  --font-sizeadd: 3.2em;
  --font-size2:1.5em;
  --font-size3:1em;
  --font-size4:1.2em;
  --font-size:0.8rem;
}


/* Apply styles to the entire page */
.custom-two-column-page {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: start;
  margin: 20px;
  gap: 20px;
  position: relative;
}

/* Styles for the left and right columns */
.custom-left-column, .custom-right-column {
  padding: 20px;
}

/* Media Query for small screens (1 column) */
@media (max-width: 768px) {
  .custom-two-column-page {
    grid-template-columns: 1fr;
  }
  .custom-two-column-page::before,
  .custom-two-column-page::after {
    display: none;
  }
}

/* Styles for the vertical line */
.custom-two-column-page::before {
  content: '';
  border-left: 2px solid var(--gray-theme);
  height: 100%;
  position: absolute;
  left: 50%; /* Center the line */
  top: 0;
  transform: translateX(-50%);
}

@import '~font-awesome/css/font-awesome.min.css';

/* Styles for the cursomcircles */
.custom-two-column-page::after {
  content: '';
  width: 20px;
  height: 20px;
  background-color: var(--Light-Rustic-Orange);
  border-radius: 50%;
  margin: 5px 0;
  position: absolute;
  left: 50%; /* Center the circles */
  /*top: 0;*/
  transform: translateX(-50%);
 /* animation: moveCircle 2s alternate infinite; /* Optional animation */
  /* Calculate the number of circles based on the line height */
 top: calc(var(--line-height) / 4); /* Adjust the number to position the circles correctly */
}


@keyframes moveCircle {
 0% {
    top: 20%; /* Adjust the number to position the circles correctly */
  }
  100% {
    top:70%; /* Adjust the number to position the circles correctly */
  }
}

/* Configuration of icons*/
.icon-list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon-link {
  text-decoration: none;
  color: #333; /* Change the color as needed */
  margin: 10px 40px;
  
}

.icon {
  font-size: 40px; /* Adjust the font size as needed */
  color: #ccc;
}
/*configuration hr*/ 
hr {
  width: 50%;
}

/*general configuration*/
.resume-container {
  margin-top: 5%;
}
.resume-container h1{
  color: gray;
  font-family: var(--Header-font1);
  font-size: var(--font-size);
}

/*Setion for skills */
.skill-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
   /* Background color of the page */
}

.cursomcircle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3); /* Gray shadow */
  
  margin: 10px;
}

/* Define different background colors for each cursomcircle */
.cursomcircle:nth-child(1) { background-color: var(--gray-theme); color: var(--black-therme); }
.cursomcircle:nth-child(2) { background-color:var(--gray-theme); color: var(--black-therme); }
.cursomcircle:nth-child(3) { background-color:var(--gray-theme); color: var(--black-therme); }
.cursomcircle:nth-child(4) { background-color:var(--gray-theme); color: var(--black-therme); }
.cursomcircle:nth-child(5) { background-color:var(--gray-theme); color: var(--black-therme); }
.cursomcircle:nth-child(6) { background-color:var(--gray-theme); color: var(--black-therme); }
.cursomcircle:nth-child(7) { background-color: var(--gray-theme); color: var(--black-therme); }
.cursomcircle:nth-child(8) { background-color:var(--gray-theme); color: var(--black-therme); }

/* Media query for smaller screens */
@media (max-width: 768px) {
  .skill-section {
    flex-direction: column; /* Display cursomcircles in a column for smaller screens */
    align-items: center;
  }

  .cursomcircle {
    margin: 10px 0; /* Add vertical spacing between cursomcircles */
  }
}
